.about-container {
  padding-top: 10rem;
  display: grid;
  grid-template-columns: 35% 50%;
  gap: 15%;
}

.about-me {
  width: 100%;
  aspect-ratio: 1/1;
  border-radius: 2rem;
  display: grid;
  place-items: center;
}

.about-me-picture {
  padding-left: 10%;
  padding-bottom: 40%;
}

.about-cards {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1.5rem;
  padding-right: 10%;
}

.about-card {
  background: var(--color-background-alt);
  border: 1px solid transparent;
  border-radius: 1rem;
  padding: 2rem;
  text-align: center;
}

.about-card:hover {
  background: transparent;
  border-color: var(--color-primary-alt);
}

.about-icon {
  color: var(--color-primary);
  font-size: 1.4rem;
  margin-bottom: 1rem;
}

.about-content p {
  margin: 2rem 0 2.6rem;
  color: var(--color-text);
  padding-right: 10%;
}

/* ==================== MEDIA QUERIES (MEDIUM & SMALL DEVICES) ===================*/
@media screen and (max-width: 1024px) {
  .about-container {
    grid-template-columns: 1fr;
    gap: 0;
  }

  .about-me {
    width: 65%;
    margin: 0 auto 3rem;
  }

  .about-cards{
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
    padding-right: 0;
  }

  .about-content {
    text-align: center;
  }

  .about-content p {
    margin: 1.5rem 0;
    padding-right: 0;
  }

  .about-me-picture {
    padding-left: 0;
  }
}
